import Link from 'next/link';

import LinkBicameral from '../components/LinkBicameral';
import LinkSecurity from '../components/LinkSecurity';
import LinkUnicameral from '../components/LinkUnicameral.js';



const Committees = () =>{
        return (

     <React.Fragment>
 
      <div className="Badges__container">
      <h1>Comites de Crisis Disponibles</h1> 
 

              <Link ><a href="/comite/unicameral">
                  <LinkUnicameral />
              </a>
              </Link>
  
              <Link ><a href="/comite/bicameral">
                  <LinkBicameral />

              </a>
              </Link>
  
              <Link><a href="/comite/security">
                  <LinkSecurity />
                </a>    
              </Link>
 

        <style jsx>{`          
          .Badges__container {
            width: 100%;
            display: grid ;
            grid-gap: 15px;
            margin-left: 10px;
            grid-template-columns: auto auto auto;
           /*  grid-template-columns: repeat(auto-fill, minmax(390px, 1fr)); */
            height: auto;
            text-align: center;
          }  
          
          
          .Badges_conf-logo {
            margin-bottom: 2rem;
            width: 260px;
          }

          
           a {
            width: 90%;
            text-decoration: none;
          }
          

        `}</style>
      </div>
       </React.Fragment> 
        )

   }   

 
 
 export default Committees;